  padding: 0;
}

button {
  background-color: transparent;
  outline: 0;
  border: 0;
}

body {
  font-family: RobotoRegular;
}

p {
  font-size: 17px;
}

.red-btn-def-wrap {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.red-btn-def-wrap .red-btn-default {
  border-radius: 20px;
  height: 40px;
  width: 200px;
  background-color: #ce3426;
  color: #fff;
  font-weight: bold;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 15px;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.red-btn-def-wrap .red-btn-default:hover {
  opacity: 0.9;
  text-decoration: none;
  color: #fff;
}
.red-btn-def-wrap .red-btn-default:active {
  opacity: 1;
}

.deftitle-sec-h3 {
  font-size: 27px;
  font-family: RobotoBold;
  color: #11e5ef;
}

.padding-y {
  padding-top: 60px;
  padding-bottom: 60px;
}

.articole-container {
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.articole-cont-header {
  min-height: 700px;
  width: 100%;
  background-color: #003852;
  background-image: url("../images/articole_header_bg.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.articole-contheader-container h2 {
  font-size: 55px;
  font-family: RobotoBold;
}
.articole-contheader-container h2:nth-child(1) {
  color: #11e5ef;
}
.articole-contheader-container h2:nth-child(2) {
  color: #ce3426;
  text-transform: uppercase;
}
.articole-contheader-container p {
  width: 50%;
  color: #fff;
  margin-top: 10px;
}

.article-preview-default,
.article-preview-selected {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  text-align: right;
}

.article-preview-selected {
  display: none;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  text-align: right;
}
.article-preview-selected h4 {
  margin-top: 20px;
}

.articole-content {
  width: 100%;
  min-height: 400px;
  background-color: #074d5f;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding-bottom: 170px;
}

.articole-content-container {
  max-width: 100%;
  width: 1400px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 15px 0 15px;
  margin-top: 50px;
}

.articolecont-halfone,
.articolecont-halftwo {
  min-height: 300px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.articolecont-halfone {
  width: 30%;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}
.articolecont-halfone h3 {
  font-size: 40px;
  font-family: RobotoBold;
  color: #fff;
}
.articolecont-halfone p {
  color: #26a8ae;
  margin-top: 20px;
  font-size: 14px;
}
.articolecont-halfone a {
  border-radius: 20px;
  height: 35px;
  width: 130px;
  background-color: #ce3426;
  color: #fff;
  font-weight: bold;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 15px;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
  margin-top: 50px;
}
.articolecont-halfone a:hover {
  text-decoration: none;
  opacity: 0.9;
}

.articolecont-halftwo {
  width: 70%;
  position: relative;
}

.articolecont-halftwo-wrapper {
  min-height: 660px;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.article-card-item {
  width: 270px;
  height: 300px;
  background-color: #fff;
  margin: 10px;
  position: relative;
}
.article-card-item a {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 3;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
}
.article-card-item a:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.articlecard-item-top {
  height: 230px;
  width: 100%;
  background-color: #000;
  padding: 10px;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  color: #fff;
}
.articlecard-item-top img {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0.5;
  z-index: 1;
}
.articlecard-item-top .articlecard-category {
  font-size: 14px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  background-color: #abb9d6;
  max-width: 170px;
  padding: 5px;
  border-radius: 5px;
  z-index: 2;
  text-align: center;
}
.articlecard-item-top h5 {
  z-index: 2;
  font-size: 17px;
  width: 100%;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
}

.articlecard-item-bottom {
  height: 70px;
  width: 100%;
  background-color: #031328;
  padding: 5px 10px 5px 10px;
}
.articlecard-item-bottom p {
  color: #e7eaf4;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  font-size: 15px;
}

.hidden {
  display: none;
}

.pagination-container {
  width: calc(100% - 2rem);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: absolute;
  bottom: -100px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.pagination-number,
.pagination-button {
  font-size: 16px;
  background-color: transparent;
  border: none;
  margin: 0.2rem 0.2rem;
  cursor: pointer;
  height: 2.5rem;
  width: 2.5rem;
  color: #11e5ef;
}

.pagination-button {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #11e5ef;
}
.pagination-button i {
  font-size: 25px;
}
.pagination-button:active {
  color: #fff;
}

.pagination-number:hover {
  opacity: 0.8;
}

.pagination-number:hover,
.pagination-button:not(.disabled):hover {
  opacity: 0.8;
}

.pagination-number.active {
  color: #fff;
}

.disabled {
  pointer-events: none;
  cursor: pointer;
}

@media (max-width: 1300px) {
  .article-card-item {
    width: 230px;
  }
}
@media (max-width: 1200px) {
  .articole-content-container {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .articolecont-halfone, .articolecont-halftwo {
    width: 100%;
  }
  .articolecont-halfone {
    text-align: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding-bottom: 30px;
  }
  .articolecont-halftwo-wrapper {
    min-height: auto;
  }
  .article-preview-default,
.article-preview-selected {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    text-align: center;
  }
}
@media (max-width: 830px) {
  .article-card-item {
    width: 40%;
  }
}
@media (max-width: 768px) {
  .articole-cont-header {
    background-position: 71% 100%;
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
  }
  .articole-contheader-container h2 {
    font-size: 40px;
  }
  .articole-contheader-container p {
    width: 100%;
  }
  .articole-contheader-container {
    text-align: center;
    min-width: 320px;
  }
  .articole-contheader-container h2 {
    font-size: 30px;
  }
  .articole-contheader-container p {
    font-size: 15px;
  }
  .articolecont-halfone h3 {
    font-size: 30px;
  }
}
@media (max-width: 630px) {
  .article-card-item {
    width: 45%;
    margin: 10px 5px 10px 5px;
  }
}
@media (max-width: 576px) {
  .articole-cont-header {
    background-position: 69% 100%;
  }
  .article-card-item {
    height: auto;
  }
  .articlecard-item-top {
    height: 200px;
  }
}
@media (max-width: 480px) {
  .articole-cont-header {
    background-position: 67% 100%;
    padding-bottom: 20px;
  }
  .article-card-item {
    max-width: 100%;
